/* vue fade in, out config */
.fade-enter-active, .fade-leave-active {
	transition-property: opacity;
	transition-duration: .15s;
  }
  
  .fade-enter-active {
	transition-delay: .15s;
  }
  
  .fade-enter, .fade-leave-active {
	opacity: 0
  }
  
  /*common*/
  .w_100{width:100% !important;}
  .w_90{width:90% !important;}
  .w_80{width:80% !important;}
  .w_70{width:70% !important;}
  .w_60{width:60% !important;}
  .w_50{width:50% !important;}
  .w_15{width:15% !important;}
  .tc{text-align: center !important;}
  .tl{text-align: left !important;}
  .display_n{display:none !important;}
  
  .content {display:block; width:100%; margin:auto; float:right;}
  
  /*header*/
  header{display:block; width:100%; padding-bottom:20px;}
  header .logo{display:block; width:100%; text-align:center; font-size:30px; font-weight:bold; padding:30px}
  header .menu{display:block; width:15%; position:absolute; padding:20px; background:white; z-index:999;}
  header .menu li{display:block; width:100%; padding:5px;}
  header .menu li a{display:block; width:100%;}
  
  header .menuBtn{background: gray;text-align: center;padding: 20px;color: white;}
  
  /*button*/
  .btnArea{display:flex;}
  .btnArea .btn{flex:1; text-align: center;}
  .btn.ins{background: skyblue; color:white;}
  .btn.upt{background: green; color:white;}
  .btn.del{background: red; color:white;}
  .btn.list{background: gray; color:white;}
  
  
  /*tab menu*/
  .tabMenu {width:50%; margin:auto;}
  .tabMenu ul {display:flex; width:100%;}
  .tabMenu ul li{flex-grow:1; text-align: center;}
  .tabMenu ul li .active{font-size:20px; font-weight:bold;}
  
  
  /*table list*/
  
  .tbl_type01{width:100%; margin:auto;}
  .tbl_type01 th,td{text-align: center; word-break: break-all}
  .tbl_type01 th{padding:10px;}
  .tbl_type01 td{text-align:left; border-bottom:1px solid #d5d5d5;}
  .tbl_type01 .left{text-align:left ;padding-left:20px}
  .tbl_type01 .on{background:gray; color:white;}
  .tbl_type01 input,select,video{width:100%;}
  .tbl_type01 video{height:250px;}
  
  /*diary*/
  .tbl_type01 .sat{color:blue;}
  .tbl_type01 .sun{color:red;}
  .tbl_type01 .preDay{background: gray; color:white;}
  .tbl_type01 .toDay{background: red; color:white;}
  
  /*aniList*/
  .aniListDayList{display:flex; width:100%; text-align: center;}
  .aniListDayList a{flex-grow:1;}
  .aniListDayList a.on{font-size:25px; font-weight:bold;}
  
  /*girls*/
  .girlsDataList{display: flex; flex-wrap: wrap; width:100%;}
  .girlsDataList li{width:20%; flex-grow: 1; word-break: break-all;}
  
  /*guestBook*/
  .commentList{display:flex; width:100%; background: #ebebeb;}
  .commentList li{flex-grow:1;}
  
  
  
  @media all and (min-width:768px) { 
	  .content{padding-left:10%; padding-right:10%;}
  }
  
  @media all and (max-width:768px) { 
	  .menu{width:100% !important;}
  }
  
  
  
  
  
